:root {
   --color-primary:#2584ff;
   --color-secondary:#00d9ff;
   --color-accent: #ff3400;
   --color-headings: #1b0760;
   --color-body: #918ca4;
   --color-border: #ccc;
   --border-radius: 30px;
   --color-body-darker:#5c5577;
   --color-footer: #232222;
}

*,*::after, *::before{
   box-sizing: border-box;
}

/* Typography */
::selection {
   background: var(--color-primary);
   color: #fff;
}

html {
   font-size: 62.5%;
}

body {
   font-family: Inter,Arial,Arial, Helvetica, sans-serif;
   color: var(--color-body);
   font-size: 2.5rem;
   line-height: 1.5;
   
}

h1, h2, h3 {
   color: var(--color-headings);
   margin-bottom: 1rem;
   line-height: 1.1;
}

h1 {
   font-size: 7rem;
}

h2 {
   font-size: 4rem;
}

h3 { 
   font-size: 3rem;
   font-weight: 500;
   
}

p {
   margin-top: 0;
}

@media screen and (min-width: 1024px) {
   body {
      font-size: 1.8rem;
   }
   
   h1 {
      font-size: 8rem;
   }

   h2 {
      font-size: 4rem;
   }

   h3 {
      font-size: 2.4rem;
   }
}

/* Links */
a {
   text-decoration: none;
}

.link-arrow {
   color: var(--color-accent);
   text-transform: uppercase;
   font-size: 2rem;
   font-weight: bold;
}

.link-arrow::after {
   content: '-->';
   margin-left: 5px;
   transition: margin 0.15s;
}

.link-arrow:hover::after {
   margin-left: 10px;
}

@media screen and (min-width:1024px) {
   .link-arrow {
      font-size: 1.5rem;
   }
}

/* Badges */ 

.badge {
   border-radius: 20px;
   font-size: 2rem;
   font-weight: 600;
   padding: 0.5rem 2rem;
   white-space: nowrap;
}

.badge--primary {
   background: var(--color-headings);
   color: white;
}

.badge--secondary {
   background-color: var(--color-secondary);
   color: white;
}

.badge--small {
   font-size: 1.6rem;
   padding: 0.5rem 1.5rem;
}

@media screen and (min-width: 1024px) {
   .badge {
      font-size: 1.5rem;
   }
   .badge--small {
      font-size: 1.2rem;
   }
}

/* Lists */
.list {
   list-style: none;
   padding-left: 0;
   
   
}

.list--inline .list__item {
display: inline-block;
margin-right: 2rem;
}

.list--tick {
   list-style-image: url(../images/tick.svg);
   padding-left: 3rem;
   color: var(--color-headings);
}

.list--tick .list__item {
   padding-left: 0.5rem;
   margin-bottom: 1rem;
}

@media screen and (min-width: 1024px) {
   .list--tick .list__item {
      padding-left: 0;
   }
}

/* Icons */
.icon {
   width:40px;
   height: 40px;
}

.icon--small {
   width:30px;
   height: 30px;
}

.icon--primary {
   fill: var(--color-primary);
}

.icon--white {
   fill: #fff;
}

.icon-container {
   align-items: center;
   background: #f3f9fa;
   border-radius:100%;
   display: inline-flex;
   height: 64px;
   justify-content: center;
   width: 64px;
}

.icon-container--accent {
   background: var(--color-accent)
}

/* Buttons */
.btn {
border-radius: 40px;
border: none;
cursor: pointer;
margin: 1rem 0;
padding: 2rem 4vw;
text-align: center;
text-transform: uppercase;
white-space:nowrap;
font-size: 1.8rem;
font-weight: 600;
}
.btn .icon {
   width: 2rem;
   height: 2rem;
   margin-right: 1rem;
   vertical-align: middle;
}

.btn--primary {
   background: var(--color-primary);
   color: white;
}

.btn--primary:hover {
background: #2982F8;
}

.btn--secondary {
   background: var(--color-secondary);
   color: white;
}

.btn--secondary:hover {
   background: #00c8eb;
}

.btn--accent {
   background: var(--color-accent);
   color: white;
}

.btn--accent:hover {
   background: #fd4516;
}

.btn--outline {
   background: none;
   border: 2px solid var(--color-headings);
   color: var(--color-headings);
}
 
.btn--outline:hover {
   background-color: var(--color-headings);
   color: white;
}

.btn--stretched {
   padding-left: 6rem;
   padding-right: 6rem;
}

.btn--block {
   width: 100%;
   display: inline-block;
}

@media screen and (min-width: 1024px) {
   .btn {
      font-size: 1.5rem;
   }
}

/* Inputs */
.input {
   padding: 1.5rem 3.5rem;
   border: 1px solid var(--color-border);
   border-radius: var(--border-radius);
   outline: 0;
   color: var(--color-headings);
   font-size: 2rem;
   
}

::placeholder {
   color: var(--color-headings);
}

.input-group {
   border: 1px solid var(--color-border);
   border-radius: var(--border-radius);
   display: flex;
   
}

.input-group .input {
   border:0;
   flex-grow: 1;
   padding: 1.5rem 2rem;
   width: 0;
}

.input-group .btn {
   margin: 4px;
}

@media screen and (min-width: 1024px) {
   .input {
      font-size: 1.5rem;
   }
   
}

/* Cards */

.card {
   border-radius: 7px;
   box-shadow: 0 0 20px 10px #f3f3f3;
   overflow: hidden;

}

.card__header, .card__body {
   padding: 2rem 3rem;
}

.card--primary .card__header {
background: var(--color-primary);
color: #fff;
}

.card--secondary .card__header {
background: var(--color-secondary);
color: #fff;
}

.card--secondary .badge--secondary{
   background: #02cdf1;
}

/* Plans  */
.plan {
   transition: transform 0.3s ease-out;
}

.plan__name {
   color: #fff;
   margin: 0;
   font-weight: 500;
   font-size: 2.4rem;
}

.plan__price {
   font-size: 6rem;
}

.plan__billing-cycle {
   font-size: 2.4rem;
   font-weight: 300;
   opacity: 0.8;
   margin-right: 1rem;
}

.plan__description {
   font-size: 2rem;
   font-weight: 300;
   letter-spacing: 1px;
   display: block;
}

.plan .list__item {
   margin-bottom: 2rem;
}

.plan--popular {
   transform: scale(1.1);
}

.plan--popular .card__header {
   position: relative;
}

.plan--popular .card__header::before {
   content: url(../images/popular.svg);
   width: 40px;
   display: inline-block;
   position: absolute;
   top: -6px;
   right: 5%;
}

.plan:hover {
   transform: scale(1.05);
}

.plan--popular:hover {
   transform: scale(1.15);
}

@media screen and (min-width: 1024px) {
   .plan__name {
      font-size: 1.4rem;
   }
   .plan__price {
      font-size: 5rem;
   }
   .plan__billing-cycle{
      font-size: 1.6rem;
   }
   .plan__description{
      font-size: 1.7rem;
   }
}

/* Media */

.media {
   display: flex;
   margin-bottom: 4rem;

}
.media__title {
   margin-top: 0;
}
.media__body {
   margin: 0 2rem;
}

.media__image {
   margin-top: 1rem;
}

/* Quotes */

.quote {
   font-size: 3rem;
   font-style: italic;
   color: var(--color-body-darker);
   line-height: 1.3;

}

.quote__text::before {
content: open-quote;
}

.quote__text::after {
   content: close-quote;
}

.quote__author {
   font-style: normal;
   font-weight: 400;
   margin-bottom: 0rem;
   font-size: 2rem
   
}

.p__quote {
   font-size: 1.5rem;
   font-style: normal;
   color: var(--color-body);
}

.quote__line {
   position: relative;
   bottom: 10px;
}

@media screen and (min-width: 1024px) {
   .quote {
     font-size: 2rem;
   }
   .quote__author {
     font-size: 2.4rem;
   }
   .p__quote {
     font-size: 1.6rem;
   }
 }
 
 /* Grids */

 .grid {
   display: grid;
 }

 @media screen and (min-width: 768px) {
   .grid--1x2 {
   grid-template-columns: repeat(2,1fr);
   }
   
 }

 @media screen and (min-width: 1024px) {
   .grid--1x3 {
   grid-template-columns: repeat(3,1fr);
   }
   
 }

 /* Testimonials */

 .testimonial {
   padding: 3rem;
 }

.testimonial__image {
   position: relative;
}

 .testimonial__image > img {
   width: 100%;
 }

 .testimonial__image > .icon-container {
   position:absolute;
   top: 3rem;
   right: -32px;
 }

 @media screen and (min-width: 768px) {
.testimonial .quote,
.testimonial .quote_author {
   font-size: 2.5rem;
}
.testimonial .quote {
   margin-left: 6rem;
}
 }

 /* Callouts */
 .callout {
   padding: 4rem;
   border-radius: 5px;
   text-align: center;
 }

 .callout--primary {
   background: var(--color-primary);
   color: #fff;
 }

 .callout__heading {
   color: #fff;
   margin-top: 0;
   font-size: 3rem;
 }

 .callout .btn {
   justify-self: center;
   align-self: center;
 }

 @media screen and (min-width: 768px) {
 
   .callout .grid--1x2 {
      grid-template-columns: 1fr auto;
   }

   .callout {
      text-align: left;
   }

   .callout .btn {
      justify-self: start;
      margin: 0 2rem;
   }
 }

 /* Collapsibles */

 .collapsible__header {
   display: flex;
   justify-content: space-between;
 }

 .collapsible__heading {
   margin-top: 0;
   font-size: 3rem;
 }

 .collapsible__chevron {
   transform: rotate(-90deg);
   transition: transform 0.3s;
 }

 .collapsible__content {
   max-height: 0;
   overflow: hidden;
   opacity: 0;
   transition: all 0.3s
 }

 .collapsible--expanded .collapsible__chevron{
    transform: rotate(0);
 }

 .collapsible--expanded .collapsible__content {
   max-height: 100vh;
   opacity: 1
 }

 /* Blocks */
 .block {
   --padding-vertical: 6rem;
   padding: var(--padding-vertical) 2rem;
   

 }

 .block__header {
   text-align: center;
   margin-bottom: 4rem;

   
 }

 .block__heading {
   margin-top: 0;
 }

 .block--dark {
   background: #000;
   color: #7b858b;
 }


 .block--dark .block__heading {
   color: #fff;
 }

 .block--skewed-right {
   clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 95%);
   padding-bottom: calc(var(--padding-vertical) + 4rem);
 }

 .block--skewed-left {
   clip-path: polygon(0% 0%, 100% 0%, 100% 80%, 0% 100%);
   padding-bottom: calc(var(--padding-vertical) + 4rem);
 }

 .container {
   max-width: 1140px;
   margin: 0 auto;
 }

 /* Navigation bar */

 .nav {
   background: #000;
   display: flex;
   justify-content: space-between;
   flex-wrap: wrap;
   padding: 0 1rem;
   align-items: center;
 }

 .nav__list{
    width: 100%;
    margin: 0;
 }

 .nav__item {
   padding: 0.5rem 2rem;
   border-bottom: 1px solid #222;
 }

 .nav__item > a {
   color: #d2d0db;
   transition: color 0.3s;
 }

 .nav__item > a :hover{
   color: #ffff;
 }

 .nav__toggler {
   opacity: 0.5;
   transition: box-shadow 0.20s;
   cursor: pointer;
 }

 .nav.collapsible--expanded .nav__toggler {
   opacity: 1;
   box-shadow: 0 0 0 3px #666;
   border-radius: 5px;
 }

 .nav__brand {
   transform: translateY(5px);
 }

 @media screen and (min-width: 768px) {
   .nav__toggler {
      display: none;
   }

   .nav__list {
      width: auto;
      display: flex;
      font-size: 1.6rem;
      max-height: 100%;
      opacity: 1;
   }

   .nav__item {
      border: 0;
   }
 }

 /* Hero */
.hero {
   clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);
}

 .hero__tagline {
   font-size: 2rem;
   color: #b9c3cf;
   letter-spacing: 2px;
   margin: 2rem 0 5rem;
 }

 .hero__img {
   width:100%;
 }

 @media screen and (min-width: 768px) {
.hero {
   padding-top: 0;
}
  
   .hero__content {
      text-align: left;
      align-self: center;
   }
 }

 /* Block domain */

 .block-domain .input-group {
   box-shadow: 0 0 30px 20px #e6ebee;
   border: 0;
   margin: 4rem auto;
   max-width: 670px;
 }

 .block-domain__prices {
   display: grid;
   grid-template-columns: repeat(3,1fr);
   grid-template-rows: repeat(2,6rem);
   font-size: 2rem;
   font-weight: 600;
   justify-content: center;
   max-width: 700px;
   margin: 0 auto;
 }

 @media screen and (min-width: 768px) {
   .block-domain__prices {
      grid-template-columns: repeat(auto-fit, minmax(100px,1fr));
   }
 }

 /* Plans Block */

 .block-plans .grid {
   gap: 8rem 4rem;
 }
 
 .block-plans .card {
   max-width: 500px;
   margin: 0 auto;
 }
   
 /* Features Plan */

 .feature {
   gap: 4rem 2rem;
   margin: 12rem 0;
 }

.feature__heading {
   margin: 1rem 0;
}


 .feature__image  {
width: 100%;
object-fit:cover;
   
 }

.feature:first-of-type {
   margin-top: 6rem;
}
 
/* Showcase Block */
.block--showcase {
   font-size: 2rem;
}

.showcase__image > img {
   width: 100%;
}

.showcase-header {
   text-align: center;
   margin-bottom: 4rem;
}

.showcase__heading{
   color: #fff;
}

.block--showcase .grid {
   gap: 4rem
}

@media screen and (min-width: 768px) {
.showcase__image {
   justify-self: end;
}

.block--showcase .grid {
   grid-template-columns: 50% 50%;
}

.showcase__image > img {
   width: auto;
   max-width: 700px;
}
}

/* Footer */

.footer {
   background: var(--color-footer);
   padding: 4rem 0;
 }

 .footer a {
   color: #777;

 }

 .footer a:hover {  
   color: #fff;
   transition: color 0.3s;

 }

 .footer__section {
   padding: 2rem;
   border-bottom: 1px solid #393939;
   }

   .footer__brand {
      margin-top: 5rem;
      text-align: center;
   }
.footer__brand img {
 width: 100%;
 max-width: 230px;
}
/* TODO: Lets check this rule if its possible to make it overall for all img */



 .footer__copyright { 
     font-size: 2.1rem;
     color: #fff;
     opacity: 0.3;

     }

     @media screen and (min-width: 768px) {
          .footer__sections {
            grid-template-columns: repeat(auto-fit,minmax(10rem,1fr));

          }

          .footer .collapsible__chevron {
            display: none;
          }

          .footer .collapsible__content {
            opacity: 1;
            max-height: 100%;
          }

          .footer__brand {
            order: -1;
            margin-top: 1rem;
          }

          .footer__copyright {
            font-size: 1.5rem;
          }

          .footer__section {
            border: 0;
          }

          .feature__heading {
            font-size: 1.6rem;
         }
      }